$font-poppins: 'Poppins', sans-serif;
$font-raleway: 'Raleway', sans-serif;

// Font sizes
$font-size-base: 1.25rem;//20px;

a, a:hover, a:visited {
  text-decoration: none
}

h1,h2,h3,h4,h5,h6 {
  font-family: $font-poppins;
  font-weight: 700;
}

h1 {
  font-size: rem(40px);
  @include for-max-size(bp-mobile-only) {
    font-size: rem(36px);
  }
}
h2 {
  font-size: rem(36px);
}
h4 {
  font-size: rem(20px);
}
h5 {
  font-size: rem(18px);
}
h6 {
  font-size: rem(16px);
  & + p {
    margin-top: 1rem;
  }
}

p {
  font-size: rem(16px);
  font-weight: 500;  
}

small {
  font-size: rem(12px);
}

.super-title {  
  font-size: rem(50px);  
  line-height: .98;
  @include for-size(bp-xlarge) {    
    font-size: rem(60px);
  }
}




